import axios from "axios";
// action
const GET_DATA = "select/GET_DATA";

// action creator
export const getData = res => ({ type: GET_DATA, res });

// initalState
const initalState = [];

axios.get("http://localhost:3001/api").then(res => {
  console.log(res.data);
  initalState = res.data;
});

// reducer
const select = (state = initalState, action) => {
  switch (action.type) {
    case GET_DATA:
      return action.res;
    default:
      return state;
  }
};
// functions

// export
export default select;
